.education {
  background: var(--bg-secondary);
  position: relative;
}

.education::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="grid" width="50" height="50" patternUnits="userSpaceOnUse"><path d="M 50 0 L 0 0 0 50" fill="none" stroke="%23e5e7eb" stroke-width="0.5" opacity="0.3"/></pattern></defs><rect width="100%" height="100%" fill="url(%23grid)"/></svg>');
  opacity: 0.5;
}

.education-timeline {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}

.education-timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  background: var(--gradient-primary);
  transform: translateX(-50%);
  border-radius: 2px;
}

.education-item {
  display: flex;
  margin-bottom: 4rem;
  position: relative;
  align-items: flex-start;
  gap: 2rem;
}

.education-item:nth-child(odd) {
  flex-direction: row;
}

.education-item:nth-child(even) {
  flex-direction: row-reverse;
}

.education-icon {
  width: 80px;
  height: 80px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  box-shadow: var(--shadow-lg);
  position: relative;
  z-index: 2;
  flex-shrink: 0;
}

.education-icon::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  border-radius: 50%;
  background: var(--gradient-primary);
  opacity: 0.3;
  animation: pulse 2s ease-in-out infinite;
}

.education-content {
  flex: 1;
  background: var(--bg-primary);
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  position: relative;
  backdrop-filter: blur(10px);
}

.education-content::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border: 15px solid transparent;
}

.education-item:nth-child(odd) .education-content::before {
  left: -30px;
  border-right-color: var(--bg-primary);
}

.education-item:nth-child(even) .education-content::before {
  right: -30px;
  border-left-color: var(--bg-primary);
}

.education-header h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.education-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
}

.education-meta span {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.education-meta svg {
  color: var(--primary-color);
  font-size: 1rem;
}

.education-description {
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.education-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.achievements h4,
.courses h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.achievements ul {
  list-style: none;
  padding: 0;
}

.achievements li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--text-secondary);
}

.achievements li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
}

.course-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.course-tag {
  background: var(--gradient-primary);
  color: white;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.course-tag:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

/* Certifications Section */
.certifications-section {
  margin-top: 4rem;
}

.subsection-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.certifications-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.certification-card {
  background: var(--bg-primary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-lg);
}

.certification-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-xl);
  border-color: var(--primary-color);
}

.certification-card h4 {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
}

.cert-issuer {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.cert-description {
  color: var(--text-secondary);
  line-height: 1.6;
}

/* Languages Section */
.languages-section {
  margin-top: 4rem;
}

.languages-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.language-item {
  background: var(--bg-primary);
  backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 15px;
  padding: 1.5rem;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-md);
}

.language-item:hover {
  transform: translateY(-3px);
  border-color: var(--primary-color);
  box-shadow: var(--shadow-lg);
}

.language-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.language-info h4 {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.language-level {
  background: var(--gradient-primary);
  color: white;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
}

.proficiency-bar {
  width: 100%;
  height: 8px;
  background: var(--bg-secondary);
  border-radius: 10px;
  overflow: hidden;
}

.proficiency-fill {
  height: 100%;
  background: var(--gradient-primary);
  border-radius: 10px;
  transition: width 0.8s ease;
}

/* Qualities Section */
.qualities-section {
  margin-top: 4rem;
}

.qualities-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin-top: 2rem;
}

.quality-tag {
  background: var(--gradient-primary);
  color: white;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  font-weight: 500;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-md);
}

.quality-tag:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

@media (max-width: 768px) {
  .education-timeline::before {
    left: 40px;
  }
  
  .education-item {
    flex-direction: row !important;
    margin-left: 2rem;
  }
  
  .education-icon {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
    position: absolute;
    left: -2rem;
  }
  
  .education-content {
    padding: 1.5rem;
    margin-left: 1rem;
  }
  
  .education-content::before {
    display: none;
  }
  
  .education-meta {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .education-details {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .certifications-grid {
    grid-template-columns: 1fr;
  }
  
  .languages-grid {
    grid-template-columns: 1fr;
  }
  
  .qualities-grid {
    justify-content: center;
  }
  
  .subsection-title {
    font-size: 1.5rem;
  }
  
  .certification-card {
    padding: 1.5rem;
  }
  
  .language-item {
    padding: 1rem;
  }
}

@media (max-width: 480px) {
  .education-item {
    margin-left: 1rem;
  }
  
  .education-icon {
    left: -1rem;
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .education-content {
    padding: 1rem;
    margin-left: 0.5rem;
  }
}
