.experience {
  background: var(--bg-primary);
  position: relative;
  overflow: hidden;
}

.experience::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="circuits" width="100" height="100" patternUnits="userSpaceOnUse"><path d="M20,20 L80,20 L80,80 L20,80 Z" fill="none" stroke="%236366f1" stroke-width="1" opacity="0.05"/><circle cx="50" cy="50" r="5" fill="%236366f1" opacity="0.1"/></pattern></defs><rect width="100%" height="100%" fill="url(%23circuits)"/></svg>');
  opacity: 0.3;
}

.experience-timeline {
  position: relative;
  max-width: 1000px;
  margin: 0 auto 4rem;
}

.experience-timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 4px;
  background: var(--gradient-primary);
  transform: translateX(-50%);
  border-radius: 2px;
}

.experience-item {
  display: flex;
  margin-bottom: 4rem;
  position: relative;
  align-items: flex-start;
  gap: 2rem;
}

.experience-item:nth-child(odd) {
  flex-direction: row;
}

.experience-item:nth-child(even) {
  flex-direction: row-reverse;
}

.experience-item.current {
  border: 2px solid var(--primary-color);
  border-radius: 20px;
  padding: 1rem;
  background: rgba(99, 102, 241, 0.05);
}

.experience-icon {
  width: 80px;
  height: 80px;
  background: var(--gradient-primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  box-shadow: var(--shadow-lg);
  position: relative;
  z-index: 2;
  flex-shrink: 0;
}

.experience-icon::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  border-radius: 50%;
  background: var(--gradient-primary);
  opacity: 0.3;
  animation: pulse 2s ease-in-out infinite;
}

.experience-content {
  flex: 1;
  background: var(--bg-primary);
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  position: relative;
  backdrop-filter: blur(10px);
}

.experience-content::before {
  content: '';
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  border: 15px solid transparent;
}

.experience-item:nth-child(odd) .experience-content::before {
  left: -30px;
  border-right-color: var(--bg-primary);
}

.experience-item:nth-child(even) .experience-content::before {
  right: -30px;
  border-left-color: var(--bg-primary);
}

.experience-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
}

.company-info h3 {
  font-size: 1.6rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.company-details {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.company-details span {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.company-details svg {
  color: var(--primary-color);
  font-size: 1rem;
}

.current-badge {
  background: var(--gradient-primary);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: var(--shadow-md);
}

.experience-description {
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.experience-details {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.responsibilities h4,
.technologies h4,
.achievements h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.responsibilities ul,
.achievements ul {
  list-style: none;
  padding: 0;
}

.responsibilities li,
.achievements li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--text-secondary);
}

.responsibilities li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
}

.achievements li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: var(--gradient-primary);
  color: white;
  padding: 0.4rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.tech-tag:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.experience-summary {
  margin-top: 4rem;
}

.summary-card {
  background: var(--bg-secondary);
  padding: 3rem;
  border-radius: 24px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  text-align: center;
  backdrop-filter: blur(10px);
}

.summary-card h3 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.summary-card p {
  font-size: 1.1rem;
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.summary-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.stat {
  text-align: center;
  padding: 1.5rem;
  background: var(--bg-primary);
  border-radius: 16px;
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.stat:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-md);
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 800;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

@media (max-width: 768px) {
  .experience-timeline::before {
    left: 40px;
  }
  
  .experience-item {
    flex-direction: row !important;
    margin-left: 2rem;
  }
  
  .experience-icon {
    width: 60px;
    height: 60px;
    font-size: 1.5rem;
    position: absolute;
    left: -2rem;
  }
  
  .experience-content {
    padding: 1.5rem;
    margin-left: 1rem;
  }
  
  .experience-content::before {
    display: none;
  }
  
  .experience-header {
    flex-direction: column;
    gap: 1rem;
  }
  
  .company-details {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .experience-details {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .summary-card {
    padding: 2rem;
  }
  
  .summary-stats {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .experience-item {
    margin-left: 1rem;
  }
  
  .experience-icon {
    left: -1rem;
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
  
  .experience-content {
    padding: 1rem;
    margin-left: 0.5rem;
  }
  
  .summary-stats {
    grid-template-columns: 1fr;
  }
}
