.contact {
  background: var(--bg-secondary);
  position: relative;
  overflow: hidden;
}

.contact::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="waves" width="100" height="100" patternUnits="userSpaceOnUse"><path d="M0,50 Q25,0 50,50 T100,50" fill="none" stroke="%236366f1" stroke-width="1" opacity="0.1"/></pattern></defs><rect width="100%" height="100%" fill="url(%23waves)"/></svg>');
  opacity: 0.3;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: start;
  position: relative;
  z-index: 2;
}

.contact-info {
  background: var(--bg-primary);
  padding: 3rem;
  border-radius: 24px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  backdrop-filter: blur(10px);
}

.contact-info h3 {
  font-size: 2.2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.contact-info p {
  font-size: 1.1rem;
  color: var(--text-secondary);
  line-height: 1.7;
  margin-bottom: 2.5rem;
}

.contact-details {
  margin-bottom: 2.5rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: var(--bg-secondary);
  border-radius: 16px;
  border: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.contact-item:hover {
  transform: translateX(5px);
  box-shadow: var(--shadow-md);
}

.contact-item svg {
  font-size: 1.8rem;
  color: var(--primary-color);
  min-width: 24px;
}

.contact-item h4 {
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.3rem;
}

.contact-item p {
  color: var(--text-secondary);
  margin: 0;
  font-size: 1rem;
}

.social-links {
  display: flex;
  gap: 1rem;
}

.social-links a {
  width: 60px;
  height: 60px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-primary);
  font-size: 1.5rem;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.social-links a:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-3px) scale(1.1);
  box-shadow: var(--shadow-lg);
}

.contact-form {
  background: var(--bg-primary);
  padding: 3rem;
  border-radius: 24px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  backdrop-filter: blur(10px);
}

.form-group {
  margin-bottom: 2rem;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 1.2rem 1.5rem;
  border: 2px solid var(--border-color);
  border-radius: 16px;
  font-size: 1rem;
  font-family: inherit;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: var(--bg-secondary);
  color: var(--text-primary);
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  background: var(--bg-primary);
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.1);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: var(--text-light);
}

.form-group textarea {
  resize: vertical;
  min-height: 140px;
}

.contact-form .btn {
  width: 100%;
  padding: 1.2rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.contact-form .btn:hover {
  transform: translateY(-2px);
}

@media (max-width: 1024px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .contact-info,
  .contact-form {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
  .contact-info,
  .contact-form {
    padding: 1.5rem;
  }
  
  .contact-info h3 {
    font-size: 1.8rem;
  }
  
  .contact-item {
    padding: 1rem;
    gap: 1rem;
  }
  
  .contact-item svg {
    font-size: 1.5rem;
  }
  
  .social-links a {
    width: 50px;
    height: 50px;
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .contact-info,
  .contact-form {
    padding: 1rem;
  }
  
  .form-group input,
  .form-group textarea {
    padding: 1rem;
  }
}

/* Loading States */
.btn-loading {
  opacity: 0.7;
  cursor: not-allowed;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 0.5rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Notification Popup */
.notification {
  position: fixed;
  top: 2rem;
  right: 2rem;
  z-index: 1000;
  max-width: 400px;
  box-shadow: var(--shadow-2xl);
  border-radius: 12px;
  overflow: hidden;
}

.notification-success {
  background: linear-gradient(135deg, #10B981 0%, #059669 100%);
  border: 1px solid #10B981;
}

.notification-error {
  background: linear-gradient(135deg, #EF4444 0%, #DC2626 100%);
  border: 1px solid #EF4444;
}

.notification-content {
  display: flex;
  align-items: center;
  padding: 1rem 1.5rem;
  color: white;
  position: relative;
}

.notification-icon {
  font-size: 1.5rem;
  margin-right: 1rem;
  flex-shrink: 0;
}

.notification-message {
  flex: 1;
  font-weight: 500;
  font-size: 0.95rem;
  line-height: 1.4;
}

.notification-close {
  background: none;
  border: none;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 4px;
  transition: background-color 0.2s ease;
  margin-left: 1rem;
  flex-shrink: 0;
}

.notification-close:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Responsive Notification */
@media (max-width: 768px) {
  .notification {
    top: 1rem;
    right: 1rem;
    left: 1rem;
    max-width: none;
  }
  
  .notification-content {
    padding: 0.875rem 1rem;
  }
  
  .notification-icon {
    font-size: 1.3rem;
    margin-right: 0.75rem;
  }
  
  .notification-message {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .notification {
    top: 0.5rem;
    right: 0.5rem;
    left: 0.5rem;
  }
  
  .notification-content {
    padding: 0.75rem;
  }
}
