.footer {
  background: var(--bg-tertiary);
  color: var(--text-primary);
  padding: 3rem 0;
  text-align: center;
  border-top: 1px solid var(--border-color);
  position: relative;
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: var(--gradient-primary);
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.footer-content p {
  font-size: 1rem;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.heart {
  color: #e74c3c;
  animation: heartbeat 1.5s ease-in-out infinite;
}

.code {
  color: var(--primary-color);
  animation: pulse 2s ease-in-out infinite;
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

.footer-links {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
}

.footer-links a {
  color: var(--text-secondary);
  text-decoration: none;
  font-weight: 500;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 8px;
}

.footer-links a:hover {
  color: var(--primary-color);
  background: var(--bg-secondary);
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .footer {
    padding: 2rem 0;
  }
  
  .footer-content {
    gap: 1.5rem;
  }
  
  .footer-links {
    gap: 1rem;
  }
  
  .footer-links a {
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .footer-links {
    flex-direction: column;
    gap: 0.5rem;
  }
}
