.skills {
  background: var(--bg-secondary);
  position: relative;
  overflow: hidden;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="hexagons" width="60" height="52" patternUnits="userSpaceOnUse"><polygon points="30,2 58,15 58,37 30,50 2,37 2,15" fill="none" stroke="%236366f1" stroke-width="1" opacity="0.1"/></pattern></defs><rect width="100%" height="100%" fill="url(%23hexagons)"/></svg>');
  opacity: 0.3;
}

.skills-container {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.skill-category {
  background: var(--bg-primary);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--border-color);
  backdrop-filter: blur(10px);
}

.category-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-align: center;
  position: relative;
}

.category-title::after {
  content: '';
  position: absolute;
  bottom: -0.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.skills-scroll-container {
  position: relative;
  overflow: hidden;
}

.skills-scroll {
  display: flex;
  gap: 1.5rem;
  overflow-x: auto;
  padding: 1rem 0;
  scroll-behavior: smooth;
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) var(--bg-secondary);
}

.skills-scroll::-webkit-scrollbar {
  height: 8px;
}

.skills-scroll::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 4px;
}

.skills-scroll::-webkit-scrollbar-thumb {
  background: var(--gradient-primary);
  border-radius: 4px;
}

.skills-scroll::-webkit-scrollbar-thumb:hover {
  background: var(--primary-dark);
}

.skill-item {
  background: var(--bg-secondary);
  border-radius: 16px;
  padding: 1.5rem;
  min-width: 200px;
  max-width: 200px;
  text-align: center;
  border: 1px solid var(--border-color);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.skill-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.skill-item:hover::before {
  transform: scaleX(1);
}

.skill-item:hover {
  box-shadow: var(--shadow-xl);
  border-color: var(--primary-color);
}

.skill-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  transition: all 0.3s ease;
}

.skill-item:hover .skill-icon {
  transform: scale(1.1) rotate(5deg);
}

.skill-info h4 {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.skill-level {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.skill-bar {
  width: 100%;
  height: 6px;
  background: var(--bg-tertiary);
  border-radius: 3px;
  overflow: hidden;
  position: relative;
}

.skill-progress {
  height: 100%;
  border-radius: 3px;
  position: relative;
  overflow: hidden;
}

.skill-progress::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.skill-percentage {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-align: center;
}

/* Scroll indicators */
.skills-scroll-container::before,
.skills-scroll-container::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 30px;
  pointer-events: none;
  z-index: 3;
}

.skills-scroll-container::before {
  left: 0;
  background: linear-gradient(to right, var(--bg-primary), transparent);
}

.skills-scroll-container::after {
  right: 0;
  background: linear-gradient(to left, var(--bg-primary), transparent);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .skill-category {
    padding: 1.5rem;
  }
  
  .skill-item {
    min-width: 180px;
    max-width: 180px;
    padding: 1.2rem;
  }
  
  .skill-icon {
    font-size: 2.2rem;
  }
}

@media (max-width: 768px) {
  .skills-container {
    gap: 1.5rem;
  }
  
  .skill-category {
    padding: 1rem;
  }
  
  .category-title {
    font-size: 1.3rem;
  }
  
  .skill-item {
    min-width: 160px;
    max-width: 160px;
    padding: 1rem;
  }
  
  .skill-icon {
    font-size: 2rem;
  }
  
  .skill-info h4 {
    font-size: 0.9rem;
  }
  
  .skill-percentage {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .skill-item {
    min-width: 140px;
    max-width: 140px;
    padding: 0.8rem;
  }
  
  .skill-icon {
    font-size: 1.8rem;
  }
  
  .skills-scroll {
    gap: 1rem;
  }
}

/* Touch devices scroll enhancement */
@media (hover: none) and (pointer: coarse) {
  .skills-scroll {
    -webkit-overflow-scrolling: touch;
  }
  
  .skill-item {
    min-width: 150px;
    max-width: 150px;
  }
}